import React, { Component } from 'react'
import classNames from 'classnames/bind'
import styles from './Testimonial.css'
import avator from 'imgs/testimonial/example1.jpg'
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap'

const cx = classNames.bind(styles)
const items = [
  {
    id: 1,
    detail: 'Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.',
    altText: 'Man A',
    caption: 'Man A',
    content: 'test',
    img: avator
  },
  {
    id: 2,
    detail: '鸟决正易孩。窗系列研另们论。德台百山斗错剧得阳议夫短导分显模体。停寻响程提线春中待。怕孤熟别毫忽部奶进战八也妙珍视剧？士打忆吧从剧致！象规站紶克连军什收很朝段印温音州。闻洗还森球床重布待弟云始需该局姐！界夜同准六模纪陈叫汽照子当质叫托七？际校每禁六摇近兴数！',
    altText: 'Man B',
    caption: 'Man B',
    content: 'test',
    img: avator
  },
  {
    id: 3,
    detail: 'Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.',
    altText: 'Man C',
    caption: 'Man C',
    content: 'test',
    img: avator
  }
]
const slides = items.map((item) => {
  return (
    <CarouselItem
      className='carouselItem'
      tag='div'
      key={item.id}
      onExiting={this.onExiting}
      onExited={this.onExited}
    >
      <div className='carouselItem__content'>
        <img src={item.img} alt='Someone' />
        <span>“</span>
        <p>{item.detail}</p>
        <div>{item.content}</div>
      </div>
      <CarouselCaption className='carouselItem__Caption' captionHeader={item.caption} />
    </CarouselItem>
  )
})
class Testimonial extends Component {
  constructor (props) {
    super(props)
    this.state = { activeIndex: 0 }
  }
  onExiting = () => {
    this.animating = true
  }

  onExited = () => {
    this.animating = false
  }

  next = () => {
    if (this.animating) return
    const nextIndex = this.state.activeIndex === items.length - 1 ? 0 : this.state.activeIndex + 1
    this.setState({ activeIndex: nextIndex })
  }

  previous = () => {
    if (this.animating) return
    const nextIndex = this.state.activeIndex === 0 ? items.length - 1 : this.state.activeIndex - 1
    this.setState({ activeIndex: nextIndex })
  }

  goToIndex = (newIndex) => {
    if (this.animating) return
    this.setState({ activeIndex: newIndex })
  }

  render () {
    const { activeIndex } = this.state

    return (
      <div className={cx('Testimonial')}>
        <div className={cx('Testimonial__header')}>
          <h1>From Our Client</h1>
        </div>
        <div className={cx('Testimonial__content')}>
          {
            <Carousel
              activeIndex={activeIndex}
              next={this.next}
              previous={this.previous}
            >
              <CarouselIndicators className={cx('Testimonial__indicators')} items={items} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
              {slides}
              <CarouselControl direction='prev' directionText='Previous' onClickHandler={this.previous} />
              <CarouselControl direction='next' directionText='Next' onClickHandler={this.next} />
            </Carousel>
          }
        </div>
      </div>
    )
  }
}

export default Testimonial



